// tab view
div.tab {

	// basement
	box-sizing:border-box;
	display:flex;

	a,
	button {
		flex:1 0 50%;
		height:52px;
		line-height:52px;
		background-color:$xx-dark-gray;
		box-sizing:border-box;

		padding:0 40px;

		text-align:center;
		border-radius:0;

		&.ing {
			border-top:2px solid $pink;
			background-color:$xxxx-dark-gray;
		}
	}
}
